import React from 'react';
import './App.css';
// import Main from './component/main/Main.js';
import Recruit from './component/recruit/Recruit.js';
import Instroduction from 'component/introduction/Introduction.js';
import Main from 'component/main/Main.js';
import Company from 'component/company/Company.js';


import {BrowserRouter, Route} from 'react-router-dom';

function App() {
  return (
    <BrowserRouter>
      {/* <div className={'parallax'}></div> */}
      <Route path="/" exact component={Main}></Route>
      <Route path="/company" exact component={Company}></Route>
      <Route path="/recruit" exact component={Recruit}></Route>
      <Route path="/introduction" exact component={Instroduction}></Route>
      {/* <div className={'parallax'}></div> */}
    </BrowserRouter>
  );
}

export default App;
