import React from 'react'
import './Person.css';
import {Col, Row} from 'react-bootstrap';

const Person = (props) => {    
    const body = props.introduction.body.map((e) => {
        return (<div>{e}</div>)
    });

    const views = (<>
                    <Col md={8} lg={{span: 4, offset: 2}}>
                        <div className={'person-title'}>
                            <span className={"person-title-intro"}>{props.introduction.intro}</span>
                            <span className={'person-title-name'}>{props.introduction.name}</span>
                        </div>
                        <div className={'person-contents'}>{body}</div>
                    </Col>
                    <Col md={4} lg={{span: 4}}>
                        <div className={'person-img-container'}>
                            <img src={props.image} className={'person-img'} alt="Person"/>
                        </div>
                    </Col>
                   </>);

    const reverse_views = (<>
                    <Col md={4} lg={{span: 4, offset: 2}}>
                        <div className={'person-img-container'}>
                            <img src={props.image} className={'person-img'} alt="Person"/>
                        </div>
                    </Col>
                    <Col md={4} lg={{span: 4}} className={"person-intro-box"}>
                        <div className={'person-title'}>
                            <span className={"person-title-intro"}>{props.introduction.intro}</span>
                            <span className={'person-title-name'}>{props.introduction.name}</span>
                        </div>
                        <div className={'person-contents'}>{body}</div>
                    </Col>
                    </>);
    
    return (
        <Row noGutters className={"person-container"}>
            {props.reverse?reverse_views:views}
        </Row>
    )
}

export default Person;